@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap");
body {
  background-color: #EFF3F6;
  background-position: bottom;
  background-position-y: 140%;
  background-repeat: no-repeat;
  background-size: contain;
  color: #000;
  font-family: "Montserrat", sans-serif;
}

#login {
  width: 370px;
}
#login #login_error {
  color: #000;
}
#login .message {
  color: #000;
}
#login h1 {
  margin-bottom: 20px;
}
#login h1 a {
  width: 180px;
  height: 60px;
  background-size: 180px 60px;
}
#login h1 a:focus {
  box-shadow: none;
}
#login form {
  border-radius: 0px;
  box-shadow: none;
  border: 1px solid #000;
  padding: 40px;
  background-color: #EFF3F6;
}
#login form label {
  color: #000;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 0.7rem;
}
#login form .input {
  border: none;
  border-radius: 0px;
  box-shadow: none;
  background-color: #EFF3F6;
  border-bottom: 1px solid #000;
  font-size: 0.9rem;
  font-weight: normal;
  color: #000;
}
#login form input[type=submit] {
  margin-top: 30px;
  display: block;
  color: #000;
  background-color: transparent;
  box-shadow: none;
  text-shadow: none;
  border-radius: 0px;
  padding: 5px 25px;
  border: 1px solid #000;
  transition: all 0.35s ease;
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: all 0.3s ease-in-out;
}
#login form input[type=submit]:hover {
  background: #fff;
}
#login #backtoblog, #login #nav {
  text-align: center;
  color: #000;
}
#login #backtoblog a, #login #nav a {
  color: #000;
  font-weight: 600;
}
#login input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
  -webkit-text-fill-color: #000 !important;
}

.wpml-login-ls form#wpml-login-ls-form select#wpml-language-switcher-locales {
  @apply rounded-none border border-solid border-black text-black bg-bg-color uppercase text-xs font-sans font-semibold transition-all duration-300 ease-in hover:bg-white;
}
.wpml-login-ls form#wpml-login-ls-form input.button {
  @apply rounded-none border border-solid border-black text-black bg-bg-color uppercase text-xs font-sans font-semibold px-4 py-1 transition-all duration-300 ease-in hover:bg-white;
}